
.sidebar {
    width: 100%;
    height: 0;
    transition: .1s;
    overflow: hidden;
    display: flex;
    flex-direction: row;
}

.sidebar.show {
    width: 100%;
    height: fit-content;
    transition: .1s;
    padding: 0;
}


.sb-menu .link-light {
    margin: 10px 0;
    text-align: start;
}

.sb-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    max-height: calc(100% - 60px);
    margin-right: 20px;
    overflow: hidden;
    padding: 0;
    transition: .1s;
    display: flex;
    flex-direction: column;
}

.sb-menu.show {
    z-index: 100;
    width: fit-content;
    padding: 20px 20px 40px 20px;
    transition: .1s;
    box-shadow: 0 0 1px 1px #aac5c7;
}

.sb-menu .logout {
    margin-top: auto;
    width: 100%;
    justify-content: flex-start;
}

.sbm-close-btn {
    width: fit-content;
}


.sb-toolbar-menu {
    width: 100%;
    transition: .3s;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
}

.sb-toolbar-menu.hide {
    height: 0;
    transition: .3s;
}

.sbtm-btn {
    margin: 0;
    padding: 20px;
    flex: 0 1 25%;
    justify-content: center;
}

.sbtm-btn > svg {
    width: 20px;
    height: 20px;
}



@media(min-width: 600px) {
    .sb-menu.show {
        width: 30%;
    }
}



@media(min-width: 900px) {
    .sidebar {
        width: 0;
        padding: 0;
        flex-direction: column;
    }

    .sidebar.show {
        height: 100%;
        max-height: 100%;
        transition: .1s;
        padding: 20px 20px 40px 20px;
        width: 200px;
        overflow: hidden;
    }

    .sb-menu {
        position: relative;
        width: 100%;
        height: calc(100% - 60px);
    }

    .sb-menu.show {
        padding: 0;
        width: 100%;
        box-shadow: none;
    }

    .sb-toolbar-menu {
        display: none;
    }

    .sbm-close-btn {
        display: none;
    }
}